* {
    box-sizing: border-box;
}

:root {
    --fs-p: 19px;
    --fs-h1: 80px;
    --fs-h2: 24px;
    --fs-span-L: 30px;
    --fs-span-B: 20px;
    --fs-span-M: 16px;
    --fs-span-S: 14px;
    --fs-span-SM: 12px;
    --fs-footer: 12px;
    --main-color: #f3ad00;
    --sub-color: #c08a00;
    --animation-button: .2s ease;
    --ff-h1: 'Changa', sans-serif;
    --ff-p: 'Jost', sans-serif;
}

body {
    /*background-color: #000;*/
    /*background-color: #272204;*/
    background-color: rgb(32, 28, 3);
    color: #fff;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

img {
    display: block;
    height: 100%;
    width: 100%;
}

p {
    font-size: var(--fs-p);
    font-family: var(--ff-p);
    line-height: 1.8;
    margin: 0;
}

h1 {
    font-family: var(--ff-h1);
    font-size: var(--fs-h1);
    line-height: 1;
    color: #f3ad00;
    text-align: center;
    margin: 0;
}

h2 {
    font-family: var(--ff-p);
    letter-spacing: 1px;
    color: #da9c00;
    font-weight: bolder;
    font-size: var(--fs-h2);
}

span {
    font-family: var(--ff-p);
}

li {
    font-family: var(--ff-p);
    font-size: var(--fs-p);
    margin-bottom: 10px;
    line-height: 1.6;
}

ul {
    margin: 0;
}

.logo {
    background-color: #1f0000;
    justify-content: center;  
    height: 12rem;
}

.image-logo {
    height: 180px;
    width: auto;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

section {
    display: flex;
    flex-direction: column;
    gap: 30px;
    justify-content: center;
    width: 60%;
    padding: 6em 0 6em 0;
}

.header {
    position: relative;
    flex-direction: row;
    align-items: flex-end;
    width: 100%;
}

.container-button {
    display: flex;
    align-items: center;
    position: absolute;
    justify-content: center;
    width: 100%;
    height: auto;
    transform: translateY(50%);
}

.mint-button {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    width: 550px;
    /*background-color: #0e140b;*/
    /*background-color: #12352c;*/
    background-color: #1f0000;
    border: 0.1px solid #e1e1e1;
    /*box-shadow: 0px 0px 6px 2px #000;*/
    height: 250px;
}

.mint-button p {
    font-size: var(--fs-span-B);
}

.token {
    align-items: center;
}

.token-button {
    padding: 1em 2em;
    border-radius: 10px;
    font-size: var(--fs-span-S);
    text-transform: uppercase;
    cursor: pointer;
    background-color: #f3ad00;
    font-family: var(--ff-p);
    font-weight: bolder;
    letter-spacing: 1px;
}

.connect-button .wallet-button {
    text-transform: uppercase;
    padding: 1em 2em;
    font-size: 14px;
    height: 60px;
    font-weight: bolder;
    margin: 0;
    background-color: #f3ad00;
    border-radius: 10px;
    transition: 0.2s ease;
}

.connect-button .wallet-button:hover {
   
    background-color: #bb8600;
}

.wallet-button * {
    font-family: var(--ff-p);
    font-size: 14px;
    color: #fff;
}

.initial {
    padding-top: 12em;
}

.text {
    width: 48.6%;
    text-align: center;
}

.image-collection {
    gap: 50px;
    height: 400px;
    display: flex;
    width: 100%;
    overflow-x: auto;
}

.row {
    display: flex;
}

.row-left {
    width: 60%;
}

.row-left img {
    max-height: 440px;
}

.row-right {
    display: flex;
    flex-direction: column;
    width: 40%;
}

.roadmap {
    /*background-color: #0e140b;*/
    /*background-color: #12352c;*/
    /*background-color: #e1e1e1;*/
    background-color: #1f0000;
}

.roadmap p {
    text-align: center;
}

.integrant {
    display: flex;
    gap: 20px;
}

.team {
    text-align: center;
}

.card-integrant {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.card-integrant img {
    margin-bottom: 12px;
}

.integrant-name {
    font-size: var(--fs-span-M);
}

.integrant-type {
    font-size: var(--fs-span-S);
    color: #9c958e;
}

.supply-container {
    /*background-color: #0e140b;*/
    /*background-color: #e1e1e1;*/
    background-color: #1f0000;
}

.supply {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.information {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.i-u {
    color: var(--main-color);
    letter-spacing: 1px;
    font-weight: bolder;
    font-size: var(--fs-span-L);
}

.i-d {
    font-size: var(--fs-span-B);
}

.faq {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.faq-page .column {
    align-items: center;
}

.faq-options {
    display: flex;
    width: 100%;
}

.faq-options div {
    width: 100%;
    text-align: center;
    padding-bottom: 8px;
    cursor: pointer;
}

.fo-m.active,
.fo-g.active {
    border-bottom: 1.5px solid #fff;
}

.faq-page h1 {
    font-family: 'Press Start 2P', cursive;
    word-spacing: -50px;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-align: center;
    font-size: 75px;
}

.faq-item {
    width: 50vw;
    min-width: 50vw;
    max-width: 50vw;
}

.faq-items {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.main-faq,
.sub-faq {
    padding: 1em 1em;
}

.sub-faq .faq-content p {
    padding: 1.5em 1em;
    transition: .2s ease;
}

.main-faq {
    background-color: #f3ad00;
    cursor: pointer;
    color: #fff;
    transition: .2s ease;
    border: 0.5px solid #000;
}

.main-faq:hover{
    background-color: #a07300;
}

.main-faq p {
    font-size: 20px;
    /* font-family: 'Montserrat', sans-serif;
    font-weight: lighter;*/
}

.main-faq .faq-content {
    justify-content: space-between;
}

.sub-faq {
    font-size: 0;
    transition: .2s ease;
}

.sub-faq p {
    font-size: 0;
}

.faq-content {
    display: flex;
    align-items: center;
    user-select: none;
}

.faq-content p {
    margin-bottom: 0;
}

.mf1.active,
.mf2.active,
.mf3.active,
.mf4.active,
.mf5.active,
.mf6.active,
.mf7.active,
.mf8.active,
.mf9.active {
    background-color: #a07300;
    border-end-start-radius: 0;
    border-end-end-radius: 0;
}

.sf1.active,
.sf2.active,
.sf3.active,
.sf4.active,
.sf5.active,
.sf6.active,
.sf7.active,
.sf8.active,
.sf9.active {
    border-start-start-radius: 0;
    border-start-end-radius: 0;
    /*color: #bebcb9;*/
    /*border: 0.5px solid #b6a9a9;*/
    border-left: 0.5px solid #000;
    border-right: 0.5px solid #000;
    border-bottom: 0.5px solid #000;
    position: static;
}

.sf1.active p,
.sf2.active p,
.sf3.active p,
.sf4.active p,
.sf5.active p,
.sf6.active p,
.sf7.active p,
.sf8.active p,
.sf9.active p {
    font-size: 19px;
    line-height: 1.5;
}

.icon-plus,
.icon-cross {
    font-weight: bolder;
    transition: .2s ease-in-out;
}

.icon-plus {
    font-size: 25px;
}

.icon-cross {
    font-size: 0;
}

.plus1.active,
.plus2.active,
.plus3.active,
.plus4.active,
.plus5.active,
.plus6.active, 
.plus7.active{
    transform: rotate(135deg);
}

.footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    font-size: var(--fs-footer);
}

.social {
    display: flex;
    align-items: center;
    justify-content: start;
    gap: 15px;
}

.image-footer {
    width: 150px;
    height: 50px;
}

.social img {
    cursor: pointer;
    width: 26px;
    height: 26px;
    transition: var(--animation-button);
}

.social img:hover {
    filter: invert(54%) sepia(97%) saturate(440%) hue-rotate(6deg) brightness(104%) contrast(103%);
}

@media (max-width:1080px) {
    .row {
        flex-direction: column;
    }
    .row-left,
    .row-right {
        width: 100%;
    }
    section,
    .text,
    .initial {
        width: 90%;
    }
    .integrant {
        flex-wrap: wrap;
        justify-content: center;
    }
    .card-integrant img {
        width: 180px;
    }
    .faq-item{
      min-width: 90vw;
      max-width: 90vw;
    }
}

@media (max-width:660px) {
     :root {
        --fs-p: 18px;
        --fs-h1: 64px;
        --fs-h2: 20px;
        --fs-span-L: 26px;
        --fs-span-B: 18px;
        --fs-span-M: 15px;
        --fs-span-S: 13px;
    }
    .logo{
      height: 7rem;
    }
    .image-logo{
      height: 100px;
    }
}

@media (max-width:380px) {
  :root {
     --fs-p: 17px;
     --fs-h1: 49px;
     --fs-h2: 19px;
     --fs-span-L: 25px;
     --fs-span-B: 17px;
     --fs-span-M: 14px;
     --fs-span-S: 13px;
 }
}